@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400;600;700&family=Roboto+Slab:wght@300;400;500;700&display=swap);
:root {
  --white: #e8e8e8;
  --white-two: #fff;
  --black-two: #121212;
  --cool-blue: #4386bf;
  --medium-blue: #307bbe;
  --azure: #0290ff;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.book-item-wrap {
  width: 100%;
  height: 10.625rem;
  padding: 2rem 9.188rem 1.625rem 1.688rem;
  border-radius: 4px;
  border: solid 1px #e8e8e8;
  border: solid 1px var(--white);
  background-color: #fff;
  background-color: var(--white-two);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.book-details {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 50rem;
  height: 6.25rem;
}

.book-item__category {
  height: 1.125rem;
  margin: 0 2.438rem 0 0;
  opacity: 0.5;
  font-family: Montserrat, serif;
  font-size: 0.875rem;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
  color: var(--black-two);
}

.book-item__title {
  height: 1.813rem;
  font-family: RobotoSlab, sans-serif;
  font-size: 1.375rem;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.2px;
  color: #121212;
  color: var(--black-two);
  text-transform: capitalize;
}

.book-item__author {
  height: 1.188rem;
  margin: 0.25rem 0 1rem 0;
  font-family: RobotoSlab, sans-serif;
  font-size: 0.875rem;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4386bf;
  color: var(--cool-blue);
  text-transform: capitalize;
}

.book-details-btns {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 4.563rem;
}

.book-details-btns > button {
  width: 4.563rem;
  height: 1.188rem;
  margin: 0.188rem 0.938rem 0.125rem 0;
  font-family: RobotoSlab, sans-serif;
  font-size: 0.875rem;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4386bf;
  color: var(--cool-blue);
  background-color: transparent;
  border: none;
}

.line-divide {
  width: 0.125rem;
  height: 1.5rem;
  margin: 0.75rem 1.063rem 0 0.938rem;
  border: solid 1px #e8e8e8;
  border: solid 1px var(--white);
}

.Line-2 {
  width: 0.125rem;
  height: 4.375rem;
  margin: 1.125rem 3.688rem 0 4.938rem;
}

.book-item__status {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.Oval-2 {
  width: 4.25rem;
  height: 4.25rem;
  border-style: inset;
  border-width: 6px;
  border-top-color: #0291ffd8;
  border-right-color: #0290ff;
  border-bottom-color: #0291fff5;
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
  border-radius: 50%;
}

.status-data {
  display: flex;
  flex-direction: column;
}

.status-data__title {
  width: 4.25rem;
  height: 2.438rem;
  margin: 0.063rem 5.75rem 0 0.688rem;
  font-family: Montserrat, serif;
  font-size: 2rem;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
  color: var(--black-two);
}

.status-data__completed {
  width: 5rem;
  height: 1.125rem;
  margin: 0.438rem 4.938rem 0.75rem 0.75rem;
  opacity: 0.5;
  font-family: Montserrat, serif;
  font-size: 0.875rem;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
  color: var(--black-two);
}

.book-item__progress {
  display: flex;
  flex-direction: column;
}

.progress-title {
  width: 100%;
  height: 1.125rem;
  margin: 0.063rem 3.625rem 0 3.688rem;
  opacity: 0.5;
  font-family: RobotoSlab, sans-serif;
  font-size: 0.813rem;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
  color: var(--black-two);
}

.progress-details {
  width: 100%;
  height: 1.313rem;
  margin: 0.438rem 6.813rem 0.25rem 3.688rem;
  font-family: RobotoSlab, sans-serif;
  font-size: 1rem;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.4px;
  color: #121212;
  color: var(--black-two);
}

.progress-update {
  width: 11.5rem;
  height: 2.063rem;
  margin: 0.75rem 0 0.563rem 3.688rem;
  padding: 0.438rem 1.188rem 0.5rem 1.375rem;
  border: none;
  border-radius: 3px;
  color: #fff;
  background-color: #0291ffd7;
}

.Line {
  width: 75rem;
  height: 0.125rem;
  margin: 2.5rem 0.063rem 1.813rem 0;
  border: solid 1px #e8e8e8;
  border: solid 1px var(--white);
}

.books-list-wrap {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 30px;
  gap: 30px;
}

.Line {
  width: 75rem;
  height: 0.125rem;
  margin: 2.5rem auto;
  border: solid 1px var(--white);
}

.add-book-title {
  width: 75rem;
  height: 1.5rem;
  display: block;
  margin-top: 1.813rem;
  margin-inline: auto;
  margin-bottom: 0.813rem;
  font-family: Montserrat, serif;
  font-size: 1.25rem;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.18px;
  color: #888;
}

.add-book-form {
  width: 70rem;
  display: flex;
  align-items: center;
  margin: 0 auto;
}

.title-input {
  width: 25rem;
  height: 2.813rem;
  margin: 1.188rem 1rem 0 0;
  padding: 0.813rem 1.063rem 0.813rem 1.063rem;
  border-radius: 4px;
  border: solid 1px var(--white);
  background-color: var(--white-two);
}

.categories-select {
  width: 15rem;
  height: 2.813rem;
  margin: 1.188rem 1rem 0 0;
  padding: 0.813rem 1.063rem 0.813rem 1.063rem;
  border-radius: 4px;
  border: solid 1px var(--white);
  background-color: var(--white-two);
}

.author-input {
  width: 22rem;
  height: 2.813rem;
  margin: 1.188rem 1rem 0 0;
  padding: 0.813rem 1.063rem 0.813rem 1.063rem;
  border-radius: 4px;
  border: solid 1px var(--white);
  background-color: var(--white-two);
}

.add-book-btn {
  width: 10rem;
  height: 2.813rem;
  margin: 1.188rem 0 0 0;
  padding: 0.801rem 1.188rem 0.886rem 1.375rem;
  border-radius: 3px;
  color: #fff;
  background-color: #0291ffd7;
}

a {
  text-decoration: none;
}

.nav-bar {
  padding: 24px 4%;
  background-color: white;
  border-bottom: solid 1px #e8e8e8;
  display: flex;
  align-items: center;
  grid-gap: 40px;
  gap: 40px;
}

.nav-brand {
  font-family: "Montserrat", sans-serif;
  font-size: 1.875rem;
  font-weight: bold;
  color: #0290ff;
}

.nav-links {
  display: flex;
  flex-grow: 1;
  list-style-type: none;
  grid-gap: 40px;
  gap: 40px;
}

.nav-link {
  opacity: 0.5;
  font-family: "Montserrat", sans-serif;
  font-size: 0.813rem;
  color: #121212;
  letter-spacing: 1.9px;
}

.active-link {
  opacity: 1;
  color: #121212;
}

@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(https://fonts.gstatic.com/s/materialicons/v140/flUhRq6tzZclQEJ-Vdg-IuiaDsNc.woff2) format('woff2');
}

.material-icons {
  font-family: 'Material Icons', 'Roboto', 'Helvetica', 'Arial', sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
}

.primary-color {
  color: #0290ff;
}

.icon-button {
  cursor: pointer;
  width: 2.813rem;
  height: 2.813rem;
  border-radius: 50%;
  border: solid 1px #e8e8e8;
  background-color: white;
}

* {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
}

body {
  padding: 4%;
  background-color: #ffffff;
}

#root {
  margin: 0;
  padding: 0 0 8% 0;
  border-radius: 4px;
  box-shadow: 0 0 60px 0 rgba(219, 158, 158, 0.05);
  background-color: #fafafa;
}

.App {
  width: 100%;
  height: 100%;
}

.container {
  width: 100%;
  padding: 4%;
}

